import React, { useEffect } from "react";
import "./experience.css";

const Experience = () => {
  useEffect(() => {
    const elements = document.querySelectorAll('.fade-up');

    const observer = new IntersectionObserver((entries, observer) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          entry.target.classList.add('fade-up-active');
          observer.unobserve(entry.target);
        }
      });
    }, { threshold: 0.3 });

    elements.forEach(element => {
      observer.observe(element);
    });

    return () => {
      elements.forEach(element => observer.unobserve(element));
    };
  }, []);

  const placeholderImg = "black_bg.png";

  const projects = {
    development: [
      {
        title: "WordPress Website Building",
        description:
          "I built Vonn.sg from scratch using WordPress, showcasing my skills in theme customization, plugin integration, and functionality enhancement. I added custom code to bring my vision to life and optimized the siteâ€™s SEO for visibility. This project highlights my expertise in crafting user-centered experiences and reinforces my commitment to impactful digital solutions as a UI/UX designer and developer.",
        video: "website_building.mp4",
      },
      {
        title: "2D Android Game Movement",
        description:
          "I developed a 2D Android game in Unity with intuitive controls using gestures, directional arrows, and smooth animations to enhance player interaction. This project refined my understanding of game mechanics and allowed me to explore innovative movement solutions, showcasing my skills as a developer and designer focused on seamless, engaging user experiences.",
        video: "freelance_video.mp4",
      },
      { title: "E-commerce Website with React", img: placeholderImg },
      { title: "Weather App with API Integration", img: placeholderImg },
      { title: "Custom Blogging Platform", img: placeholderImg },
      { title: "Real-Time Chat Application", img: placeholderImg },
    ],
    design: [
      {
        title: "Website Portfolio Design",
        description:
          "I designed a visually captivating website portfolio in Adobe Illustrator, focusing on an intuitive layout, cohesive color scheme, and engaging typography for an optimal user experience. This project highlights my UI/UX skills, showcasing my ability to create visually stunning, user-friendly designs that deliver a seamless digital experience.",
        video: "portfolio_design.mp4",
      },
      { title: "Branding & Logo Design", img: placeholderImg },
      { title: "Mobile App UI Design", img: placeholderImg },
      { title: "E-commerce Website Design", img: placeholderImg },
      { title: "UI/UX Redesign for News Website", img: placeholderImg },
      { title: "Digital Magazine Design", img: placeholderImg },
    ],
  };

  const [activeCategory, setActiveCategory] = React.useState("development");
  const [modalProject, setModalProject] = React.useState(null);

  const openModal = (project) => setModalProject(project);
  const closeModal = () => setModalProject(null);

  return (
    <div className="experience-container">
      <section className="skills-section fade-up">
        <ul>
          {[
            { src: "nodejsLogo.png", name: "NodeJS & Express" },
            { src: "htmlLogo.png", name: "HTML5" },
            { src: "cssLogo.png", name: "CSS" },
            { src: "pythonLogo.png", name: "Python" },
            { src: "phpLogo.png", name: "PHP" },
            { src: "reactLogo.png", name: "React" },
            { src: "dbLogo.png", name: "MongoDB & MySQL" },
            { src: "javascriptLogo.png", name: "JavaScript" },
            { src: "vrLogo.png", name: "VR Development (HTC Vive)" },
            { src: "aiLogo.png", name: "Illustrator" },
            { src: "psLogo.png", name: "Photoshop" },
          ].map((skill, index) => (
            <li key={index} className="fade-up">
              <img src={skill.src} alt={skill.name} className="skill-icon" />
              {skill.name}
            </li>
          ))}
        </ul>
      </section>

      <section className="experience-header fade-up">
        <h1 className="experience-title">MY PORTFOLIO</h1>
      </section>

      <div className="category-toggle fade-up">
        {["development", "design"].map((category) => (
          <button
            key={category}
            className={`toggle-button ${activeCategory === category ? "active" : ""}`}
            onClick={() => setActiveCategory(category)}
          >
            {category.charAt(0).toUpperCase() + category.slice(1)}
          </button>
        ))}
      </div>

      <section className="projects-section fade-up">
        <div className="projects-container fade-up">
          {projects[activeCategory].map((project, index) => (
            <div key={index} className="project-card fade-up" onClick={() => openModal(project)}>
              {project.video ? (
                <video className="project-video" src={project.video} />
              ) : (
                <img src={project.img} alt={project.title} />
              )}
              <h3>{project.title}</h3>
            </div>
          ))}
        </div>
      </section>

      {modalProject && (
        <div className="modal" onClick={closeModal}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            {modalProject.video ? (
              <video className="modal-video" src={modalProject.video} controls />
            ) : (
              <img src={modalProject.img} alt={modalProject.title} />
            )}
            <h3>{modalProject.title}</h3>
            <p>{modalProject.description}</p>
            <button className="close-btn" onClick={closeModal}>Close</button>
          </div>
        </div>
      )}

      <section className="footer-section fade-up">
        <h2>Want to get in touch?</h2>
        <p>Feel free to contact me for any inquiries!</p>
        <div className="social-links">
          {[
            { href: "https://www.tiktok.com", src: "tiktokIcon.png", name: "Tiktok" },
            { href: "https://discord.com", src: "discordIcon.jpg", name: "Discord" },
            { href: "https://www.instagram.com", src: "instagramIcon.jpg", name: "Instagram" },
            { href: "https://www.linkedin.com", src: "linkedinIcon.png", name: "LinkedIn" },
          ].map((link, index) => (
            <a key={index} href={link.href} target="_blank" rel="noopener noreferrer">
              <img src={link.src} alt={link.name} />
              {link.name}
            </a>
          ))}
        </div>
      </section>
    </div>
  );
};

export default Experience;
